import $T from '@/common/time.js';
export default {
	props:{
		item:Object,
		index:Number,
		pretime:[Number,String]
	},
	computed: {
		// 是否是登录用户本人
		isSelf() {
			let myId = this.$store.state.user.id
			return myId === this.item.user_id
		},
		// 转化时间
		shortTime(){
			return $T.getChatTime(this.item.create_time,this.pretime)
		}
	},
}
